import React from 'react'
import {Form, Button } from 'react-bootstrap'
import emailjs from 'emailjs-com'
import { FaGithub, FaLinkedin} from 'react-icons/fa'
const Contact = () => {

const GITHUB_URL = 'https://github.com/joseMHernandez'
const LINKEDLN_URL = 'https://www.linkedin.com/in/josemanuel-hernandez'

  const sendEmail = (e) => {
    e.preventDefault();
    
    emailjs.sendForm('service_npfwish', 
    'template_5a9hs8n', 
    e.target, 
    'user_F6nrDo8jWLgx6k7qLQCkl').then(res =>{
      console.log(res)
    }).catch(err=>{
      console.log(err)
    })

    e.target.reset();
  } 
    return (
      <>
        <main className='form-container'>

<div className='title-container'>
                <h1 className='title'>Get in Touch!</h1>
                <div className='underline'></div>
        </div>

        
        <Form className='form'onSubmit={sendEmail} >

        <Form.Group className="mb-3" >
          <Form.Label>Your Name:</Form.Label>
          <Form.Control name='user_name' className='box-form' type="text" placeholder="Enter Name" />
          
        </Form.Group>

        <Form.Group className="mb-3" >
          <Form.Label>Email address:</Form.Label>
          <Form.Control  name='user_email' className='box-form' type="email" placeholder="Enter email"  />
          
        </Form.Group>
      
      
        <Form.Group className="mb-3" >
    <Form.Label>Message</Form.Label>
    <Form.Control name='user_message' as="textarea" rows={4} placeholder='message' />
  </Form.Group>
        <Button variant="primary" type="submit">
          Submit
        </Button>

        <div className='icon-box'>
      <a href={GITHUB_URL} >
      <FaGithub className='icon' />
      </a>

       <a href={LINKEDLN_URL }>
       <FaLinkedin className='icon' />
       </a>
        </div>

      </Form>
       </main>
       </>
    )
}

export default Contact
